.slider.module > .grid-container {
  position: relative;
  padding-top: 5.25em;
  padding-bottom: 1.95em; }
  .slider.module > .grid-container:before {
    content: '';
    position: absolute;
    top: 0;
    left: 16.7%;
    right: -16.7%;
    bottom: 0;
    background-color: #F2F9ED; }

.slider.module .left .inner .slider-nav {
  position: relative;
  padding-top: 1.9em;
  width: 100%;
  display: flex;
  justify-content: flex-end; }
  .slider.module .left .inner .slider-nav button:last-child {
    margin-left: 1.1em; }

.slider.module .right .inner .text-slider .slick-list {
  padding: 0 !important;
  margin-left: -50px;
  margin-right: -50px; }
  .slider.module .right .inner .text-slider .slick-list .text-wrap {
    padding: 0;
    margin: 0 50px; }

.slider.module .right .inner .text-slider ul.slick-dots {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin-left: -.7em;
  margin-right: -.7em; }
